* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    
}

.header {
    display: flex;
    /* border:1px solid black; */
    justify-content: space-evenly;
    font-weight: 200;
    font-size: 1.5rem;
    height: 10vh;
    background-color: #464d4c;
    color: aliceblue;
    align-items: center;
    color: bisque;
    background-color: rgba(89, 37, 247, 0.921);
}

.chatSection {
    border-top: 1px solid black;
    height: 90vh;
    display: flex;
    color: bisque;
    background-color: rgba(125, 85, 243, 0.921);
}

.mainchat {
    /* border:1px solid black; */
    flex-basis: 70%;
    /* max-width: 70%; */
    background-color: #EDEDED;
}

.chatDisplay {
    border: 1px solid black;
    height: 93%;
    overflow-y: scroll;
    /* background-color: rgba(201, 147, 252, 0.921); */
    /* background-color: #F2F2F2;  */
    background-color: #fce3e3; /* light gray */
}

.typeDiv {
    border: 1px solid black;
    height: 7%;
    display: flex;
}

.joinedMembers {
    /* border:1px solid black; */
    width: 30%;
    display: flex;
    flex-direction: column;
    /* background-color: #666c6b;
    color: aliceblue; */
    font-size: 1.2rem;
    text-align: center;
}

.joinedMembers div {
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
    padding: 0.5rem;

}
.length{
    height: 10%;
    box-shadow: 2px 2px 2px 2px rgba(91, 40, 242, 0.921);
    margin:1rem;
}
.membersList{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 70%;
    max-height: 80%;
    overflow-y:auto;
    box-shadow: 2px 2px 2px 2px rgba(91, 40, 242, 0.921);
    margin: 1rem;
    text-align: center;
    flex-basis: 30%;
    align-items: center;
    
    /* min-width: 400px; */
}

.membersList div{
    overflow-wrap: break-word;
}

#input {
    height: 99%;
    width: 85%;
    background-color: rgb(214, 244, 214);
    border-bottom: 1px solid black;
    border-top: none;
    border-radius: 0%;
    display: flex;
    align-items: center;
}

button {
    height: 100%;
    width: 15%;
    background-color: rgb(144, 238, 144);
    border: none;
}

