.entryPage {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: bisque;
    background-color: rgba(152, 67, 231, 0.921);

}

.formContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
}

.entryForm {
    display: flex;
    align-items: center;
    background-color: blueviolet;
    width: 40%;
    min-width: 350px;
    height: 55vh;
    flex-wrap: wrap;
    font-size: 1.2rem;
    justify-content: center;
    box-shadow: 4px 4px 4px 4px rgb(118, 17, 212);
}

.formsComponent {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
}

input,
select {
    min-width: 80%;
    border-radius: 4%;
    margin-top: 0.1rem;
    display: block;
    padding: 0.5rem;
    background-color: rgb(225, 170, 248);
}

label {
    font-size: 1.5rem;
    min-width: 80%;
    border-radius: 4%;
    margin-top: 0.1rem;
    display: block;
    padding: 0.5rem;
}


input[type='submit'] {
    background-color: rgb(47, 138, 242);
    color: rgb(238, 213, 229);
    font-size: larger;
    text-shadow: 1px 1px black;
    font-weight: 700;
}